"use client"

import { useState } from 'react'
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Checkbox } from "@/components/ui/checkbox"
import { ScrollArea } from "@/components/ui/scroll-area"
import { Separator } from "@/components/ui/separator"
import { Mail, Star, Trash2, Send, Archive, File, AlertCircle } from 'lucide-react'

interface Email {
  id: number
  sender: string
  subject: string
  message: string
  time: string
  read: boolean
  starred: boolean
}

export default function Home() {
  const [emails, setEmails] = useState<Email[]>([
    {
      id: 1,
      sender: "John Doe",
      subject: "Meeting Tomorrow",
      message: "Hi, let's discuss the project tomorrow at 10 AM.",
      time: "10:30 AM",
      read: false,
      starred: false
    },
    {
      id: 2,
      sender: "Jane Smith",
      subject: "Project Update",
      message: "Here's the latest update on the ongoing project.",
      time: "9:15 AM",
      read: true,
      starred: true
    },
    // Add more sample emails
  ])

  const [selectedEmails, setSelectedEmails] = useState<number[]>([])
  const [currentView, setCurrentView] = useState('inbox')

  const toggleEmailSelection = (id: number) => {
    setSelectedEmails(prev => 
      prev.includes(id) 
        ? prev.filter(emailId => emailId !== id)
        : [...prev, id]
    )
  }

  const toggleStar = (id: number) => {
    setEmails(prev => 
      prev.map(email => 
        email.id === id 
          ? { ...email, starred: !email.starred }
          : email
      )
    )
  }

  return (
    <div className="min-h-screen bg-white">
      {/* Header */}
      <header className="flex items-center justify-between p-4 border-b">
        <div className="flex items-center space-x-4">
          <img src="/vercel.svg" alt="Gmail" className="h-8 w-8" />
          <h1 className="text-xl font-semibold">Gmail</h1>
        </div>
        <div className="flex-1 max-w-2xl mx-4">
          <Input 
            type="search" 
            placeholder="Search mail" 
            className="w-full"
          />
        </div>
      </header>

      {/* Main Content */}
      <div className="flex h-[calc(100vh-73px)]">
        {/* Sidebar */}
        <aside className="w-64 border-r p-4">
          <Button 
            className="w-full mb-4 bg-blue-100 text-blue-600 hover:bg-blue-200"
          >
            Compose
          </Button>

          <nav className="space-y-1">
            <Button
              variant="ghost"
              className={`w-full justify-start ${currentView === 'inbox' ? 'bg-blue-50 text-blue-600' : ''}`}
              onClick={() => setCurrentView('inbox')}
            >
              <Mail className="mr-2 h-4 w-4" />
              Inbox
            </Button>
            <Button
              variant="ghost"
              className={`w-full justify-start ${currentView === 'starred' ? 'bg-blue-50 text-blue-600' : ''}`}
              onClick={() => setCurrentView('starred')}
            >
              <Star className="mr-2 h-4 w-4" />
              Starred
            </Button>
            <Button
              variant="ghost"
              className={`w-full justify-start ${currentView === 'sent' ? 'bg-blue-50 text-blue-600' : ''}`}
              onClick={() => setCurrentView('sent')}
            >
              <Send className="mr-2 h-4 w-4" />
              Sent
            </Button>
            <Button
              variant="ghost"
              className={`w-full justify-start ${currentView === 'drafts' ? 'bg-blue-50 text-blue-600' : ''}`}
              onClick={() => setCurrentView('drafts')}
            >
              <File className="mr-2 h-4 w-4" />
              Drafts
            </Button>
            <Button
              variant="ghost"
              className={`w-full justify-start ${currentView === 'spam' ? 'bg-blue-50 text-blue-600' : ''}`}
              onClick={() => setCurrentView('spam')}
            >
              <AlertCircle className="mr-2 h-4 w-4" />
              Spam
            </Button>
            <Button
              variant="ghost"
              className={`w-full justify-start ${currentView === 'trash' ? 'bg-blue-50 text-blue-600' : ''}`}
              onClick={() => setCurrentView('trash')}
            >
              <Trash2 className="mr-2 h-4 w-4" />
              Trash
            </Button>
          </nav>
        </aside>

        {/* Email List */}
        <main className="flex-1">
          <div className="p-4 border-b flex items-center space-x-4">
            <Checkbox 
              checked={selectedEmails.length === emails.length}
              onCheckedChange={(checked) => {
                if (checked) {
                  setSelectedEmails(emails.map(e => e.id))
                } else {
                  setSelectedEmails([])
                }
              }}
            />
            <Button variant="ghost" size="icon">
              <Archive className="h-4 w-4" />
            </Button>
            <Button variant="ghost" size="icon">
              <Trash2 className="h-4 w-4" />
            </Button>
            <Separator orientation="vertical" className="h-6" />
          </div>

          <ScrollArea className="h-[calc(100vh-145px)]">
            {emails.map((email) => (
              <div
                key={email.id}
                className={`flex items-center p-4 border-b hover:bg-gray-50 cursor-pointer ${
                  !email.read ? 'font-semibold bg-blue-50' : ''
                }`}
              >
                <Checkbox 
                  checked={selectedEmails.includes(email.id)}
                  onCheckedChange={() => toggleEmailSelection(email.id)}
                  className="mr-4"
                />
                <button
                  onClick={() => toggleStar(email.id)}
                  className="mr-4"
                >
                  <Star 
                    className={`h-4 w-4 ${
                      email.starred ? 'fill-yellow-400 text-yellow-400' : 'text-gray-400'
                    }`}
                  />
                </button>
                <div className="flex-1 min-w-0">
                  <div className="flex justify-between items-start">
                    <span className="block truncate">{email.sender}</span>
                    <span className="text-sm text-gray-500 ml-2">{email.time}</span>
                  </div>
                  <div className="text-sm text-gray-900 truncate">{email.subject}</div>
                  <div className="text-sm text-gray-500 truncate">{email.message}</div>
                </div>
              </div>
            ))}
          </ScrollArea>
        </main>
      </div>
    </div>
  )
}
